	module.exports = {"data":{"post":{"html":"<p><a href=\"http://www.flickr.com/photos/papascott/59423643/\" title=\"Radical Trim\"><img src=\"http://static.flickr.com/24/59423643_6a189d654c.jpg\" width=\"500\" height=\"375\" alt=\"IMG_0442\" /></a></p>\n<p>We just trimmed our wild roses down to the ground and had several trees removed, and we've discovered things in our backyard that we had forgotten we had. Like natural light. And neighbors.</p>\n<p>Update: Here are a couple of 'before' pictures that show how high the roses were. They grow like weeds, so in a year or two they'll be that high again.</p>\n<p><a href=\"http://www.flickr.com/photos/papascott/59672929/\" title=\"Backyard 1\"><img src=\"http://static.flickr.com/31/59672929_9c81076be1_m.jpg\" width=\"240\" height=\"180\" alt=\"IMG_0326\" /></a></p>\n<p><a href=\"http://www.flickr.com/photos/papascott/59678105/\" title=\"Backyard 2\"><img src=\"http://static.flickr.com/26/59678105_4e618cdb23_m.jpg\" width=\"240\" height=\"180\" alt=\"IMG_0333\" /></a></p>","fields":{"title":"Radical Trim","date":"03 Nov 2005","url":"https://papascott.de/2005-11-03-radical-trim/"}}},"pathContext":{"slug":"/2005-11-03-radical-trim/","prev":{"fields":{"title":"Knastblog","date":"28 Oct 2005","slug":"/2005-10-28-knastblog/"}},"next":{"fields":{"title":"Things An American Can't Do In Germany: Open An Account with Fidelity","date":"03 Nov 2005","slug":"/2005-11-03-things-an-american-cant-do-in-germany-open-an-account-with-fidelity/"}}}}

